.footer {
  @include contentWidth;
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: space-between;
  padding: 40px 0;

  @include onTablet {
    padding: 20px 0;
  }

  @include onMobile {
    grid-template-columns: repeat(2, auto);
    row-gap: 15px;
    padding: 45px 0 50px;
  }

  &__nav {
    @include onMobile {
      grid-row-start: span 2;
      grid-column-start: 1;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    column-gap: 40px;

    @include onMobile {
      grid-template-columns: 1fr;
      column-gap: normal;
      row-gap: 12px;
    }
  }

  &__logo {
    grid-column-start: 1;
  }

  &__link {
    @include font(14px, $regular, 150%);
    color: $grey2;

    cursor: pointer;

    @include onMobile {
      font-size: 13px;
    }
  }

  &__soc-media {
    display: grid;
    grid-template-columns: repeat(4, auto);
    column-gap: 17px;
  }

  &__soc-img {
    cursor: pointer;
  }

  &__contacts {
    display: none;

    @include onMobile {
      display: block;

      grid-row-start: 2;
      grid-column-start: 2;
    }
  }

  &__phone,
  &__email {
    display: block;

    color: $grey;
    font-size: 13px;

    cursor: pointer;
  }

  &__email {
    margin-top: 12px;
  }
}
