.video-button {
  display: flex;
  align-items: center;
  height: 70px;

  background-image: url(../images/playVideo.svg);
  background-repeat: no-repeat;

  &__play {
    position: relative;

    display: block;
    margin-left: 27px;

    border: 11px solid transparent;
    border-left: 18px solid $blue;

    @include hover(border-left-color, $white, 0.3s);

  }

  &__play-video {
    padding-left: 50px;

    font-weight: $medium;
  }
}
