.tech-specs {
  width: 1060px;
  height: 800px;

  overflow: hidden;

  @include onLaptop {
    width: 970px;
    height: 681px;
  }

  @include onTablet {
    @include contentWidth;
    height: 600px;
  }

  @include onMobile {
    height: 365px;
  }

  &__title {
    text-align: right;
    margin: 160px 120px 0 0;

    @include onLaptop {
      margin: 100px 80px 0 0;
    }

    @include onTablet {
      margin: 80px 0 0;

      text-align: left;
    }

    @include onMobile {
      margin-top: 50px;
    }
  }

  &__presentation {
    position: relative;

    display: flex;
    justify-content: center;
    margin-top: 91px;

    @include onTablet {
      margin-top: 57px;
    }

    @include onMobile {
      margin-top: 41px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 25%;
      left: 365px;

      display: block;
      width: 52px;
      height: 201px;

      background-image: url(../images/elipse.png);
      background-repeat: no-repeat;

      @include onLaptop {
        left: 320px;
      }

      @include onTablet {
        display: none;
      }
    }
  }

  &__desc {
    position: absolute;
    width: 250px;
    
    @include onTablet {
      padding: 15px 10px;

      background-color: $bgPopUpColor;
      border-radius: 4px;
      opacity: 0;

      transition: 0.4s;
    }

    @include onMobile {
      width: 200px;
      padding: 10px;
    }

    &--one {
      top: -10px;
      left: 0;

      @include onTablet {
        top: 25%;
        left: 70px;
      }

      @include onMobile {
        left: 5%;

        width: 180px;
      }
    }

    &--two {
      left: 0;
      top: 70%;

      @include onTablet {
        top: 70%;
        left: 45%;
      }

      @include onMobile {
        top: 75%;
        left: 40%;
      }
    }

    &--three {
      top: -10px;
      right: -90px;
      width: 340px;

      @include onLaptop {
        right: -25px;
        width: 338px;
      }

      @include onTablet {
        top: -15%;
        right: 5%;
      }

      @include onMobile {
        top: 13%;
        right: 3%;

        width: 200px;
      }
    }
  }

  &__line {
    position: absolute;

    display: block;

    @include onTablet {
      display: none;
    }

    &--top {
      top: 0;
      right: 50%;
      height: 80px;
      width: 430px;

      @include onLaptop {
        width: 390px;
      }
    }

    &--left {
      top: 40%;
      left: 145px;
      height: 115px;
      width: 220px;

      @include onLaptop {
        width: 170px;
      }
    }

    &--right {
      top: 0;
      right: 270px;
      height: 240px;
      width: 260px;

      @include onLaptop {
        right: 325px;

        width: 160px;
      }
    }
  }

  &__wrapper {
    position: relative;
  }

  &__button {
    position: absolute;
    z-index: 10;

    display: none;

    &--active {
      animation: fade 0.9s ease 1 forwards;
      z-index: 10;
    }

    @keyframes fade {
      from {
        opacity: 0;
      }

      to {
        opacity: 1;
      }
    }

    @include onTablet {
      display: block;
    }

    &--1 {
      top: -35px;
      left: 70px;

      @include onMobile {
        left: 27%;
        top: -5%;
      }
    }

    &--2 {
      right: 20px;
      top: 150px;

      @include onMobile {
        left: 70%;
        top: 40%;
      }
    }

    &--3 {
      left: -30px;
      top: 270px;

      @include onMobile {
        top: 80%;
      }
    }
  }

  &__plus {
    $plusWidht: 20px;

    position: relative;

    width: 60px;
    height: 60px;

    background-color: $blue;
    border-radius: 50%;
    border: 2px solid transparent;

    cursor: pointer;

    @include onMobile {
      width: 34px;
      height: 34px;
    }

    @include onMobile {
      $plusWidht: 12px;
    }

    &::after {
      content: "";

      position: absolute;
      top: 50%;
      left: calc(50% - #{$plusWidht / 2});

      width: $plusWidht;
      height: 1px;

      background-color: $white;
    }

    &::before {
      content: "";

      position: absolute;
      top: calc(50% - #{$plusWidht / 2});
      left: calc(50% - 1px);

      width: 1px;
      height: $plusWidht;

      background-color: $white;
    }

    &:hover {
      background-color: $bntBgHover;
      border-color: rgba(255, 255, 255, 0.14);

      &::before {
        display: none;
      }
    }
  }

  &__subtitle {
    @include font(18px, 700, 23px);

    @include onTablet {
      display: none;
    }
  }

  &__text {
    margin-top: 20px;

    line-height: 150%;
    color: $grey;

    @include onTablet {
      margin-top: 0;
    }

    @include onMobile {
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__device {
    margin-top: 49px;

    @include onTablet {
      display: none;
    }

    &--tablet {
      display: none;

      @include onTablet {
        display: block;
      }

      @include onMobile {
        display: none;
      }
    }

    &--mobile {
      display: none;

      @include onMobile {
        display: block;
      }
    }
  }
}
