.contact-us {
  @include contentWidth;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 155px;

  @include onLaptop {
    padding-top: 100px;
  }

  @include onTablet {
    padding: 70px 0;

    column-gap: 20px;
  }

  @include onMobile {
    padding: 50px 0;
    grid-template-columns: 1fr;
  }

  // ! left side

  &__caption {
    @include font(14px, 400, 18px);
    color: $blue;

    @include onTablet {
      margin-top: 24px;
    }
  }

  &__title {
    margin-top: 12px;

    @include onTablet {
      margin-top: 10px;
    }
  }

  &__sub-caption {
    margin-top: 24px;

    color: $grey;

    &--mobile {
      display: none;

      @include onMobile {
        display: block;
        margin-top: 27px;

        color: $grey;
      }
    }

    @include onTablet {
      margin-top: 48px;
    }

    @include onMobile {
      display: none;
    }
  }

  &__contacts {
    margin-top: 75px;
    display: flex;
    flex-direction: column;

    @include onMobile {
      display: none;
    }
  }

  &__phone,
  &__email {
    display: block;
    margin-top: 7px;

    color: $grey;
  }

  &__phone {
    margin-bottom: 3px;
  }

  // ! right side

  &__right {
    position: relative;

    padding-left: 10px;

    @include onMobile {
      padding-left: 0;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__input,
  &__textarea {
    width: 100%;
    margin-top: 25px;

    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid $borderInputColor;
    color: $white;
    line-height: 150%;
  }

  &__input-item {
    position: relative;
  }

  &__input {
    padding-bottom: 6px;
  }

  &__textarea {
    margin-bottom: 50px;

    resize: none;

    &::placeholder {
      color: $grey;
      text-indent: -2px;
    }

    &:focus {
      border-color: $blue;

      &::placeholder {
        color: $blue;
      }
    }

    @include onMobile {
      margin-bottom: 30px;
    }
  }

  &__hint {
    position: absolute;
    top: 26px;
    left: 0;

    color: $grey;
    font-size: 14px;

    transition: 0.3s;

    cursor: text;
  }

  &__input:focus,
  &__input:not(:placeholder-shown) {
    border-color: $blue;
    
    & + .contact-us__hint {

      top: 5px;

      color: $blue;
    }
  }

  &__button {
    margin-bottom: 205px;

    @include onLaptop {
      margin-bottom: 100px;
    }

    @include onTablet {
      margin-bottom: 0;
    }

    @include onMobile {
      width: 100%;
    }
  }

  &__arrow-wrap {
    position: absolute;
    bottom: 200px;
    right: 0;

    cursor: pointer;

    @include onLaptop {
      bottom: 100px;
    }

    @include onTablet {
      display: none;
    }
  }

  &__arrow {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 48px;
    height: 48px;

    border: 1px solid $blue;
    border-radius: 50%;
    background-repeat: no-repeat;

    transition: 0.3s ease;

    &::after {
      content: "";

      border: solid $blue;
      border-width: 0 2px 2px 0;
      display: inline-block;
      padding: 4px;

      transform: rotate(-135deg);
      -webkit-transform: rotate(-135deg);
    }

    &:hover {
      background-color: $blue;

      &::after {
        border-color: $white;
      }
    }
  }
}
