.about-product {
  @include centered;
  @include contentWidth;

  overflow: hidden;

  &__top {
    margin: 147px 0 195px;
    display: grid;
    column-gap: 90px;
    grid-template-columns: 3fr 2fr;

    @include onLaptop {
      margin: 105px 0 201px;
      column-gap: 80px;
      grid-template-columns: 1.1fr 1fr;
    }

    @include onTablet {
      margin: 70px 0 88px;
      column-gap: 20px;
      grid-template-columns: 1fr 1fr;
    }

    @include onMobile {
      margin: 50px 0 79px;
      grid-template-columns: 1fr;
    }
  }

  &__top-left {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;

    & > .about-product__title {
      display: none;

      @include onMobile {
        display: block;
        margin-bottom: 20px;
      }
    }

    @include onLaptop {
      margin-top: 0;
    }

    @include onTablet {
      margin-top: 0;
      justify-content: flex-start;
    }

    @include onMobile {
      flex-direction: column;
    }
  }

  &__right {
    & > .about-product__title {
      @include onMobile {
        display: none;
      }
    }
  }

  &__counter {
    margin-right: 20px;

    @include font(14px, 400, 18px);
    color: $blue;

    @include onTablet {
      display: none;
    }
  }

  &__paginations {
    margin-top: 12px;

    @include onTablet {
      display: none;
    }
  }

  &__slider {
    @include onMobile {
      width: 100%;
    }

    @include onMobile-down {
      width: 100%;
    }
  }

  &__btn-group {
    display: none;
    grid-template-columns: repeat(5, max-content);
    column-gap: 6px;
    justify-content: center;
    margin-top: 15px;

    @include onTablet {
      display: grid;
    }
  }

  &__btn-item {
    width: 4px;
    height: 4px;

    background-color: $btnColor;
    border-radius: 50%;

    cursor: pointer;

    &--active {
      background-color: $btnActive;
    }
  }

  &__title-style {
    display: inline-block;
  }

  &__text {
    margin-top: 25px;

    line-height: 150%;
    color: $grey;

    @include onMobile {
      margin-top: 20px;
    }
  }

  &__video {
    margin-top: 25px;

    @include onLaptop {
      margin-top: 20px;
    }

    @include onTablet {
      margin-top: 22px;
    }

    @include onMobile {
      margin-top: 15px;
    }
  }

  &__image {
    &-ipad {
      display: none;

      @include onTablet {
        display: block;
      }

      @include onMobile {
        width: 100%;
      }

      @include onMobile-down {
        display: none;
      }
    }

    &-small {
      display: none;

      @include onMobile-down {
        display: block;
        width: 100%;
      }
    }

    @include onLaptop {
      width: 100%;
      height: 290px;
    }

    @include onTablet {
      display: none;
    }
  }

  &__bottom {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 162px;

    @include onLaptop {
      margin-bottom: 102px;
    }

    @include onTablet {
      margin-bottom: 35px;
    }

    @include onMobile {
      margin-bottom: 50px;
    }
  }

  &__bottom-left {
    width: 532px;
    margin-right: 110px;

    @include onLaptop {
      width: 429px;
      margin-right: 50px;
    }

    @include onTablet {
      width: 60%;
      margin-right: 35px;
    }

    @include onMobile {
      width: 100%;
      margin-right: 0;
    }
  }

  &__greeting {
    margin-bottom: 14px;

    @include font(14px, 400, 18px);
    color: $blue;

    @include onMobile {
      margin-bottom: 10px;
    }
  }

  &__bottom-right {
    height: 340px;
    transform: rotate(-90deg);
    margin-top: -30px;

    @include onLaptop {
      width: 355px;
    }

    @include onTablet {
      width: 260px;
      margin-top: -50px;
    }

    @include onMobile {
      display: none;
    }
  }

  &__decoration {
    margin-top: 17px;

    @include font(64px, 700);
    color: transparent;
    -webkit-text-stroke: 1px $blue;

    @include onTablet {
      font-size: 48px;
    }

    &:nth-child(1) {
      transform: rotate(180deg);
      margin-bottom: 17px;
    }

    &:nth-child(2) {
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.5);
    }

    &:nth-child(3) {
      transform: rotate(180deg);
      margin-bottom: 17px;
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.2);
    }

    &:nth-child(4) {
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.1);
    }

    &:nth-child(5) {
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.05);
    }

    &:nth-child(6) {
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.03);
    }

    &:nth-child(7) {
      -webkit-text-stroke: 1px rgba(5, 194, 223, 0.02);
    }
  }
}
