.nav {
  display: flex;
  align-items: center;

  &__list {
    position: relative;
    z-index: 3;

    display: grid;
    grid-template-columns: repeat(4, max-content);
    column-gap: 34px;
    margin-right: 32px;

    @include onLaptop {
      margin-right: 34px;
    }

    @include onTablet {
      display: none;
    }
  }

  &__list-item {
    position: relative;

    &::after {
      content: "";

      position: absolute;
      left: 0;
      bottom: -7px;
      display: block;

      width: 10px;
      height: 2px;
      background-color: $white;
      opacity: 0;
    }

    &:hover::after {
      opacity: 1;
      animation: scale 2s ease forwards;
    }

    @keyframes scale {
      to {
        width: 100%;
      }
    }
  }

  &__link {
    display: inline-block;
    line-height: 35px;
  }

  &__button {
    position: relative;
    z-index: 3;

    @include onTablet {
      display: none;
    }
  }

  &__burger {
    position: absolute;

    display: block;
    height: 1px;

    background-color: $white;

    transition: transform 0.3s;

    &--1 {
      width: 24px;
    }

    &--2 {
      content: "";

      top: 7px;
      left: -6px;

      width: 18px;
    }

    &--3 {
      content: "";

      top: 14px;

      width: 24px;
    }
  }

  &__burger-container {
    position: relative;
    z-index: 11;

    display: none;
    width: 30px;
    height: 20px;

    cursor: pointer;

    $rotation: 45deg;
    $translateY: (14px / 2);
    $translateX: 0;

    @include onTablet {
      display: block;
    }

    &--active {
      .nav__burger--1 {
        transform: translateY($translateY)
          translateX($translateX) rotate($rotation);
      }

      .nav__burger--2 {
        transform: translateX(10px);
        opacity: 0;
      }

      .nav__burger--3 {
        transform: translateY($translateY * -1)
          translateX($translateX) rotate($rotation * -1);
      }
    }

    &:hover .nav__burger--1,
    &:hover .nav__burger--2,
    &:hover .nav__burger--3 {
      background-color: $blue;
      transition: 0.3s;
    }
  }

  &__menu-bar {
    display: none;

    transition: 0.3s ease;

    @include onTablet {
      position: absolute;
      left: 0;
      top: -700px;
      z-index: 10;
      
      width: 100%;
      height: 568px;
      display: block;
      padding-top: 97px;

      background-color: #191536;
    }

    @include onMobile {
      padding-top: 75px;
    }

    &--active {
      top: 0;
    }
  }

  &__menu-item {
    height: 49px;
    padding-left: 154px;

    cursor: pointer;

    @include hover(background-color, #110E25, 0.3s);

    @include onMobile {
      padding-left: 22px;
    }
  }

  &__menu-link {
    display: inline-block;

    line-height: 49px;
    font-size: 21px;

    @include hover(color, $blue, 0.3s);
  }
}
