.container {
  width: 100%;

  &__header {
    @include centered;

    background: linear-gradient(107.56deg, #191536 0%, #000 100%);
    overflow: hidden;
  }

  &__about-product {
    @include centered;

    background: linear-gradient(146.86deg, #191536 -0.5%, #000 99.5%);
  }

  &__tech-specs,
  &__contact-us,
  &__footer,
  &__gaming {
    @include centered;

    background: $bgBlack;
  }

  &__kat-loco {
    @include centered;

    background: linear-gradient(108.62deg, #191536 0%, #000 77.34%);
  }
}
