.header {

  @include contentWidth;
  // Header Top

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 47px;

    font-weight: $medium;

    @include onTablet {
      margin-top: 30px;
    }

    @include onMobile {
      margin-top: 18px;
    }
  }

  &__top-left {
    display: flex;
  }

  &__logo {
    display: block;
    margin-right: 34px;
  }

  &__current-lang {
    position: relative;
    color: $blue;
    cursor: pointer;

    @include onTablet {
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      right: -15px;
      top: 8px;

      display: block;
      width: 9px;
      height: 9px;

      background-image: url(../images/blueArrowDown.svg);
      background-repeat: no-repeat;
    }
  }

  &__lang__list {
    display: none;
  }

  // Header Middle 

  &__middle {
    position: relative;
    z-index: 2;
    margin-top: 232px;

    @include onLaptop {
      margin-top: 101px;
    }

    @include onTablet {
      margin-top: 55px;
    }
  }

  &__title {
    margin-left: 20px;

    @include font(54px, 700);

    @include onLaptop {
      margin-left: 0;

      font-size: 46px;
    }

    @include onTablet {
      margin-left: 0;

      font-size: 36px;
    }

    @include onMobile {
      font-size: 26px;
    }
  }

  &__title-style {
    font-family: $secondaryFont;
    color: $blue;
  }

  &__content {
    margin: 30px 0 0 112px;

    @include onLaptop {
      margin: 14px 0 0 92px ;
    }

    @include onTablet {
      margin: 14px 0 61px;
    }
  }

  &__text {
    width: 440px;

    @include font(20px, 500, 150%);

    color: $grey;

    @include onLaptop {
      width: 336px;

      font-size: 16px;
    }

    @include onTablet {
      width: 340px;

      font-size: 16px;
    }

    @include onMobile-down {
      width: 100%;
    }
  }

  &__price {
    margin-top: 30px;

    @include font(20px, 500, 25px);

    @include onLaptop {
      margin-top: 15px;

      font-size: 18px;
    }

    @include onTablet {
      margin-top: 10px;

      font-size: 18px;
    }

    @include onMobile-down {
      margin-top: 11px;

      text-align: center;
    }
  }

  &__video {
    margin-top: 35px;

    @include onLaptop {
      margin-top: 19px;
    }

    @include onTablet {
      margin-top: 23px;
    }

    @include onMobile {
      margin-top: 15px;
    }
  }

  &__device {
    position: absolute;
    right: -150px;
    top: -270px;
    z-index: 1;

    width: 68%;
    object-fit: contain;

    @include onLargeScreen {
      top: -200px;
    }

    @include onLaptop {
      top: -150px;
    }

    @include onTablet {
      width: 70%;

      top: -90px;
      right: -60px;
    }

    @include onMobile-down {
      display: none;
    }
  }

  &__device-small {
    display: none;

    @include onMobile-down {
      display: block;
      width: 100%;

      object-fit: contain;
      object-position: -20px;
    }
  }

  //Header bottom

  &__bottom {
    display: flex;
    justify-content: space-between;
    margin: 200px 0 121px;

    line-height: 150%;
    font-weight: $medium;

    @include onLaptop {
      margin: 84px 0 97px;
    }

    @include onTablet {
      display: none;
    }
  }

  &__faq {
    margin-right: 34px;
  }

  &__more {
    position: relative;
    color: $blue;
    transition: 0.3s;

    &::after {
      content: "";
      position: absolute;
      left: calc(50% - 9px / 2);
      bottom: -17px;

      display: block;
      width: 9px;
      height: 9px;

      background-image: url(../images/blueArrowDown.svg);
      background-repeat: no-repeat;

      transition: 0.3s;
    }
  }

  &__scroll-down {
    margin-left: 100px;

    cursor: pointer;

    &:hover .header__more {
      color: $white;

      &::after {
        background-image: url(../images/whiteArrowDown.svg);
      }
    }
  }
}
