.paginations {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 102px;
  width: 207px;
  border-bottom: 1px solid $lineColor;

  &__page {
    position: relative;
    
    line-height: 35px;
    display: block;
  
    &--active {
      color: $grey;
  
      &::after {
        position: absolute;
        left: 0;
        bottom: 0;
  
        content: "";
        width: 70px;
        height: 1px;
        background-color: $blue;
      }
    }
  }
}
