.gaming {
  @include contentWidth;
  @include centered;

  @include onMobile-down {
    align-items: flex-start;
  }

  &__title {
    margin-top: 158px;

    @include onLaptop {
      margin-top: 100px;
    }

    @include onTablet {
      margin-top: 70px;
    }

    @include onMobile {
      margin-top: 50px;
    }
  }

  &__caption {
    display: block;
    margin-top: 14px;
    @include font(14px, 400, 18px);
    color: $blue;

    @include onTablet {
      margin-top: 10px;
      
      @include font(12px, 400, 15px);
    }
  }

  &__features {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 20px;
    margin: 69px 0 166px;

    @include onLaptop {
      margin: 82px 0 112px;
    }

    @include onTablet {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
      margin: 30px 0 78px;
      gap: 40px 20px;
    }

    @include onMobile {
      grid-template-columns: 1fr;
      margin: 30px 0 50px;
      row-gap: 20px;
    }
  }

  &__features-item {
    @include centered;

    @include onMobile {
      align-items: flex-start;
    }
  }

  &__image {
    @include onMobile {
      display: none;
    }
  }

  &__subtitle {
    margin-top: 33px;

    @include font(18px, 700, 23px);

    @include onTablet {
      @include font(16px, 700, 20px);
    }

    @include onMobile {
      margin-top: 0;
    }
  }

  &__text {
    margin-top: 17px;

    line-height: 150%;
    text-align: center;
    color: $grey;

    @include onTablet {
      margin-top: 20px;
    }

    @include onMobile {
      text-align: left;
    }
  }
}
